		jianNum(){
			let prices = 0
			if(this.buyNum > 1){
				this.buyNum -= 1
				
				prices = (this.price * this.buyNum / (this.buyNum + 1)).toFixed(2)
				// console.log(prices)
				this.price = prices
			}
			if(this.buyNum <= 1){
				return
			}
